import React, {useState} from 'react';

 const Formulario = (props) => {

        const initUser = {id: null, name: '', job: ''};
    
        const [user, setUser] = useState(initUser);
    
        const handleChange = e => {
            const {name, value} = e.target;
            setUser({...user, [name]: value});
        }
    
        const handleSubmit = e => {
            e.preventDefault();
            if (user.name && user.job) {
               handleChange(e, props.addUser(user));
            }
        }
    
        return (
            <form>
                <label>Name</label>
                <input className="u-full-width" type="text" value={user.name} name="name" onChange={handleChange} />
                <label>Job</label>
                <input className="u-full-width" type="text" value={user.job} name="job" onChange={handleChange} />
                <button className="button-primary" type="submit" onClick={handleSubmit} >Agregar</button>
            </form>
        )
    }

export default Formulario;