import React from 'react';
import Usuario from './Usuario'


const Header = () => {
    const user = {
        name: "Jonathan",
        surname: "Bravetti"
    }
    return (
        <header className= "encabezado">
                <div><h1>Striata</h1></div>
                <div><Usuario user={user}/></div>
        </header>
    );
}
export default Header;